@import url(https://fonts.googleapis.com/css2?family=Barlow:ital,wght@0,100;0,200;0,300;0,400;0,600;0,700;1,100;1,200;1,400;1,500;1,700&display=swap);
body {
  background: #87b5b5;
  font-size: 18px;
  font-family: Barlow, sans-serif, Helvetica;
  }

 
  .App {
    max-width: 100%;
   
  }

  h1{
    text-align: center;
  }


  .wraper{
    margin: 20px auto;
    padding: 10px;
    display: flex;
    flex-direction: column;
  }
  
  .editor-div, .preview-div {
    padding: 0 10px 10px;
    border: 2px solid black;
    border-radius: 5px;
  }
s
  .editor-div, h4 {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: #4aa3a3;
    padding: 10px;
    border-bottom: 2px solid black;
    margin: 0;
  }

  .editor-div {
    padding: 0;
    margin: auto;
    width: 50%;
  }

  .preview-div{
    padding: 0;
    min-height: 250px;
    margin: 30px auto;
    width: 70%;
  }


  textarea, #preview {
    background: #87b5b5;
    font-size: 18px;
  }
 
  textarea {
    min-height: 150px;
    width: 99.5%;
    resize: vertical;
    border: none;
    overflow: auto;
    outline: none;
    box-shadow: none;
  }
  



